.about {
    position: relative;
    padding-top: 60px;
    padding-bottom: 36px;
    text-align: center;

    @media screen and (min-width: 768px) {
    padding-bottom: 53px;
    }

    @media screen and (min-width: 1200px) {
        padding-bottom: 60px;
    }
}

.about__top {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    transform: translate(0, 6px);
    display: block;
    width: 100%;
    height: 26px;

    background-color: #D9D9D9;

    @media screen and (min-width: 768px) {
        display: none;
    }
}

.about__label {
    margin-bottom: 12px;

    font-weight: 800;
    font-size: 22px;
    line-height: 1.2;

    text-transform: uppercase;

    color: var(--copyright-text-color);

    @media screen and (min-width: 1200px) {
        font-size: 30px;
    }
}

.about__title {
    margin-bottom: 32px;

    font-family: 'Seymour One';
    font-weight: 400;
    font-size: 34px;
    line-height: 1.3;

    letter-spacing: 0.06em;
    text-transform: uppercase;

    color: var(--copyright-text-color);

    @media screen and (min-width: 768px) {
        margin-bottom: 40px;
    }

    @media screen and (min-width: 1200px) {
        margin-bottom: 82px;
        font-size: 36px;
    }
}

.about-content {
    @media screen and (min-width: 768px) {
        display: flex;
        align-items: center;
    }
}

.about-content__overlay {
    margin-bottom: 62px;
    position: relative;

    @media screen and (min-width: 768px) {
       margin-right: 32px;
    }

    @media screen and (min-width: 1200px) {
        margin-right: 108px;
    }
}

.about-content__circle {
    margin-left: auto;
    margin-right: auto;
    display: block;
    width: 278px;
    height: 278px;
    border-radius: 50%;

    background: linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)), #D9D9D9;
    box-shadow: 0px 4.48583px 16.8218px rgba(153, 114, 23, 0.25);

    @media screen and (min-width: 768px) {
        width: 334px;
        height: 330px;
    }

    @media screen and (min-width: 1200px) {
        width: 490px;
        height: 490px;
    }
}

.about-content__img {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    transform: translate(0, 20px);
    width: 278px;
    height: 278px;

    @media screen and (min-width: 768px) {
        width: 334px;
        height: 330px;
    }

    @media screen and (min-width: 1200px) {
        width: 490px;
        height: 490px;
    }
}

.about-content__wrapper {
    @media screen and (min-width: 768px) {
        text-align: left;
    }
}

// .about-content__list {
   
// }

.about-content__item {
    min-width: 220px;

    & {
        @media screen and (min-width: 768px) {
            width: 334px;
            margin-bottom: 8px;    
        }

        @media screen and (min-width: 1200px) {
            width: 463px;
        }
    }

    &:nth-child(n+2) {
        @media screen and (max-width: 767px) {
            margin-bottom: 26px;
        }
    }

    &:last-child {
        @media screen and (max-width: 767px) {
            display: none;
        }

        @media screen and (min-width: 768px) {
            margin-bottom: 48px;
        }
    }
}

.about-content__text {
    font-size: 12px;
    line-height: 1.75;

    color: var(--copyright-text-color);

    @media screen and (min-width: 1200px) {
        font-size: 14px;
        line-height: 1.9;
    }
}

// Button

.about-content__btn {
    padding: 12px 34px;
    align-items: center;
    text-align: center;
    
    font-family: inherit;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.3;
    border-radius: 10px;

    background-color: var(--accent-color);
    color: #000000;

    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    color 300ms cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
        background-color: #0a8817;
        color: #fff;
        transform: scale(1.1);
        outline: none;
        box-shadow: 0 8px 30px rgba(0, 0, 0, 0.4);
    }
}